@import url('https://fonts.googleapis.com/css2?family=Bangers&family=Roboto+Mono:wght@500&display=swap');

body {
  margin: 0;
  min-height: 100vh;
  background-color: #daf5ff;

  font-family: "Roboto Mono", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  font-family: "Bangers", cursive;
  width: 100vw;
  padding: 8px 18px;
  font-size: 44px;
  box-sizing: border-box;
  color: black;
  background-color: #87f6ff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 6px;
}

.navbar a {
  text-decoration: none;
  color: black;
}

.view-all {
  font-family: "Roboto Mono";
  font-size: 22px;
  padding: 4px 8px;
  border: 3px solid white;
  border-radius: 4px;
  background-color: #44ffd2;
  cursor: pointer;
  transition: 250ms;
  text-align: center;
}

.form-header{
  font-size: 32px;
  background-color: #616163;
  color: white;
  margin-bottom: 8px;
}

.input-field {
  display: flex;
  flex-direction: column;
  margin-bottom: 4px;
  font-size: 22px;
}

.input-field  input {
  width: 320px;
  height: 30px;
  border-radius: 6px;
  font-size: inherit;
  outline: none;
  border: 3px solid #616163;
}

.submit-btn {
  width: 200px;
  padding: 4px;
  box-sizing: border-box;
  font-family: inherit;
  font-size: 22px;
  background-color: #44ffd2;
  border-radius: 4px;
  border: 3px solid white;
  transition: 250ms;
  overflow: hidden;
}

.submit-btn:hover, .view-all:hover {
  transform: translateX(5px);
  background-color: #ffbfa0;
}

form > div {
  padding-left: 8px;
}

form > button {
  margin-left: 8px;
}

form > .button-div {
  margin-bottom: 8px;
}

form p {
  margin: 0;
}

.error-field {
  color: red;
  font-size: 18px;
  margin-left: 8px;
}

.button-div {
  display: flex;
  gap: 8px;
}

.product-page > div {
  padding-left: 8px;
} 

.product-info {
  font-size: 32px;
  color: black;
  background-color: #ffbfa0;
  text-transform: capitalize;
  display: flex;
  gap: 8px;
  align-items: center;
}

.stock-info {
  background-color: #87f6ff;
  color: black;
  padding: 0px 4px;
  border-radius: 4px;
  font-size: 28px;
}

.compact-view {
  background-color: #3882f6;
  color: white;
  padding-left: 8px;
  font-size: 22px;

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 50px;
  align-items: center;
}

.compact-stock-info {
  height: 100%;
  display: grid;
  align-items: center;
  padding-left: 8px;
  background-color: #616163;
}

.compact-view a {
  color: white;
}

.compact-product-name {
  text-transform: capitalize;
}

.indicator {
  height: 50px;
  font-size: 34px;
  text-align: center;
}

.all-products {
  display: flex;
  flex-direction: column;
  gap: 4px;
}


